<template>
  <v-app-bar dark elevate-on-scroll color="#36213E" class="my-4" flat absolute>
    <v-btn @click.prevent="showItemPersonCards" icon>
      <v-icon>mdi-home-outline</v-icon>
    </v-btn>

    <v-spacer></v-spacer>

    <v-btn @click.prevent="showFilter" icon>
      <v-icon>mdi-filter-outline</v-icon>
    </v-btn>

    <v-btn @click.prevent="showProfileActivities" icon>
      <v-icon>mdi-account-outline</v-icon>
    </v-btn>

    <v-menu left bottom>
      <template v-slot:activator="{ on, attrs }">
        <v-btn v-bind="attrs" v-on="on" icon>
          <v-icon>mdi-dots-vertical</v-icon>
        </v-btn>
      </template>
      <v-list>
        <v-list-item
          v-for="(options, index) in options"
          :key="index"
          @click="(activateOption(options.pageName))"
        >
          <v-list-item-title>
            {{ options.title }}
          </v-list-item-title>
        </v-list-item>
      </v-list>
    </v-menu>
  </v-app-bar>
</template>

<script>
export default {
  data() {
    return {
      options: [
        { title: "Become VIP", pageName: "GetVip" },
        { title: "Create business profile", pageName: "CreateProfile" },
        { title: "Contact us", pageName: "ContactUs" },
        { title: "Take a tour", pageName: "Tour" },
      ],
    };
  },

  methods: {
    activateOption(pageName) {
      console.log("method invoked");
      console.log(pageName);
      this.$router.push({ name: pageName });
    },
    showItemPersonCards() {
      this.$store.commit("setFilterStatus", false);
      this.$router.push({ name: "ItemPersonCards" });
      // window.location.reload();
    },
    showFilter() {
      this.$router.push({ name: "Filter" });
    },
    showProfileActivities() {
      this.$router.push({ name: "MyDeals" });
    },
    showOptions() {
      this.$router.push({ name: "ContactUs" });
    },
  },
};
</script>

<style>
</style>


